<template>
  <section class="search">
    <!-- 第一行 -->
    <div class="nav c-gap-inner-left-20">
      <div
        v-for="(item, index) in searchTab.list"
        :key="index"
        class="nav-item c-btn transition-animate"
        :class="{
          'selected': searchTab.curSelectedIndex === index
        }"
        @click="handleTagsClick({
          item,
          index
        })">
        {{item.text}}
      </div>
    </div>
    <!-- 搜索表单 -->
    <el-row
      class="search-form">
      <el-col
        v-for="(item, index) in searchList"
        :key="index" 
        :span="8">
        <!-- 选择器 -->
        <el-select 
          v-model="item.value" 
          placeholder="请选择"
          :style="{
            'width': '100%'
          }">
          <el-option
            v-for="(item2, index2) in item.options"
            :key="index2"
            :label="item2.value"
            :value="item2.value">
          </el-option>
        </el-select>
      </el-col>
      <!-- 搜索按钮 -->
      <el-col
        :span="8">
        <el-button 
          type="success"
          :style="{
            'width': '100%'
          }"
          @click="handleSearchClick">
          开始搜索
        </el-button>
      </el-col>
    </el-row>
  </section>
</template>

<script>
export default {
  name: "Search",
  data() {
    return {
      searchTab: {
        curSelectedIndex: 0,
        list: [
          {
            text: "品牌搜索"
          }
        ]
      },
      searchList: [
        // 车辆类型
        {
          key: "type",
          text: "汽车类型",
          value: "新车&二手车",
          options: [
            {
              value: "新车&二手车"
            },
            {
              value: "新车"
            },
            {
              value: "二手车"
            },
            {
              value: "认证汽车"
            }
          ]
        },
        // 汽车型号
        {
          key: "model",
          text: "汽车型号",
          value: "所有型号",
          options: [
            {
              value: "所有型号"
            },
            {
              value: "型号1"
            },
            {
              value: "型号2"
            },
            {
              value: "型号3"
            }
          ]
        },
        // 预算上限
        {
          key: "maxPrice",
          text: "预算上限",
          value: "没有上限",
          options: [
            {
              value: "没有上限"
            },
            {
              value: "$2000"
            },
            {
              value: "$4000"
            },
            {
              value: "$6000"
            }
          ]
        },
        // 汽车品牌
        {
          key: "brand",
          text: "汽车品牌",
          value: "所有品牌",
          options: [
            {
              value: "所有品牌"
            },
            {
              value: "奔驰"
            },
            {
              value: "奥迪"
            },
            {
              value: "宝马"
            }
          ]
        },
        // 距离
        {
          key: "distance",
          text: "距离",
          value: "不限距离",
          options: [
            {
              value: "不限距离"
            },
            {
              value: "10公里"
            },
            {
              value: "20公里"
            },
            {
              value: "30公里"
            }
          ]
        }
      ]
    };
  },
  created() {},
  methods: {
    handleTagsClick(obj) {
      console.log("handleTagsClick obj", obj);
      this.searchTab.curSelectedIndex = obj.index;
    },
    // 搜索按钮点击
    handleSearchClick() {
      console.log("搜索按钮点击 handleSearchClick");
      this.$emit("searchBtnClick");
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../../assets/app";
.search {
  font-size: 14px;
  color: #fff;
  text-align: center;
  box-sizing: border-box;
  .nav {
    display: flex;
    align-items: center;
    height: 80px;
    line-height: 80px;
    background: $primaryColor;

    .nav-item {
      width: 150px;
      height: 38px;
      line-height: 38px;
      border: 1px solid #fff;
      box-sizing: border-box;

      &:hover {
        background: #fff;
        color: #000;
      }
    }

    .selected {
      position: relative;
      background: #fff;
      color: #000;

      &::after {
        position: absolute;
        content: "";
        bottom: -20px;
        left: 63px;
        width: 0;
        height: 0;
        border-width: 10px 12px;
        border-style: solid;
        border-color: #fff transparent transparent transparent;
      }
    }
  }
  .search-form {
    flex-wrap: wrap;
    padding: 0 20px 15px 20px;
    background: $primaryColor;
    box-sizing: border-box;
  }
}
</style>



