import Vue from 'vue';
import Vuex from 'vuex';

// 使用 Vuex
Vue.use(Vuex);

// 如果在模块化构建系统中，请确保在开头调用了 Vue.use(Vuex)
export default new Vuex.Store({
  state: {
    // 是否登录状态
    isLogin: false
  },
  mutations: {
    /**
     * 设置登录状态
     * @param {*} state
     * @param {*} isLogin
     */
    setLoginState(state, isLogin) {
      state.isLogin = isLogin;
    }
  }
});



